import { GET_FAVORITE_LIST, CANCEL_FAVORITE, ADS_LIST, APPLY_JOBS } from "@/api/api";
import dashboardAdsImg from '@/assets/ads/2.png';
import meSideMenu from "@/components/meSideMenu";
import { encode } from 'js-base64';
import applyJobButton from '@/components/jobs/applyButton';
import { updateWindowHeight } from "@/utils/tools";
export default {
  name: "favorites",
  components: {
    meSideMenu,
    applyJobButton
  },

  data() {
    return {
      dashboardAdsImg,
      favoriteData: [],
      page: 1,
      limit: 8,
      totalNum: 0,
      adsDataTop: [],
      type: 0,
      identity: 0,
      applyBtnLoading: false,
      applyJobId: 0,
      favoritedSortValue: 'desc'
    };
  },

  unmounted() {
    updateWindowHeight();
    window.onresize = null;
  },

  mounted() {
    let screenWidth = document.body.clientWidth;
    let screenWidthFloor = Math.floor(screenWidth);

    if (screenWidthFloor <= 768) {
      updateWindowHeight();
    }

    window.onresize = () => {
      if (screenWidthFloor <= 768) {
        updateWindowHeight();
      }
    };

    let identity = localStorage.getItem('identity');
    this.identity = identity; // 1.jobs 2.deals 3.Event 4.educator 5.recruitor 6.school 7.other 8.vendor

    if (identity == 1) {
      this.type = 1;
    }

    if (identity == 2) {
      this.type = 4;
    }

    if (identity == 3) {
      this.type = 4;
    }

    if (identity == 4) {
      this.type = 4;
    }

    if (identity == 5) {
      this.type = 4;
    }

    this.getFavoriteList(this.type, this.page, this.limit); // this.getAdsList()
  },

  methods: {
    changeFavoritedSort(value) {
      this.favoritedSortValue = value;
      this.getFavoriteList(this.type, 1, this.limit);
    },

    turnDealDetail(id) {
      let obj = {
        id: id
      };
      let {
        href
      } = this.$router.resolve({
        path: '/deals/detail',
        query: obj
      });
      window.open(href, '_blank');
    },

    applyJob(id) {
      this.applyBtnLoading = true;
      this.applyJobId = id;
      let identity = localStorage.getItem('identity');
      let token = localStorage.getItem('token');

      if (identity == 1) {
        let params = {
          job_id: id,
          token: token
        };
        APPLY_JOBS(params).then(res => {
          if (res.code == 200) {
            this.$message.success('Apply Success');
            this.applyBtnLoading = false;
          }
        }).catch(err => {
          console.log(err);
          this.$message.error(err.msg);
          this.applyBtnLoading = false;
        });
      } else {
        this.$message.warning('Please switch to an educator profile to be able to apply');
        this.applyBtnLoading = false;
      }
    },

    turnBusinessProfile(info) {
      let obj = {
        jobId: info.id,
        uid: info.user_id,
        i: info.identity,
        cid: info.company_id
      };
      let {
        href
      } = this.$router.resolve({
        path: '/jobs/business/profile',
        query: obj
      });
      window.open(href, '_blank'); // this.$router.push({path:'/jobs/business/profile',query:obj})
    },

    viewEducatorDetail(info) {
      console.log(info);
      let companyId = info.user_contact.company_id;
      let userId = info.user_contact.id;
      let obj = {
        cid: companyId,
        uid: userId,
        identity: 1,
        from: 'other'
      };
      let str = encode(JSON.stringify(obj));
      let {
        href
      } = this.$router.resolve({
        path: '/educator/profile',
        query: {
          str: str
        }
      });
      window.open(href, '_blank'); // this.$router.push({path: '/educator/profile', query: {str: str}})
    },

    changeType(type) {
      this.type = type;
      this.favoriteData = [];
      this.page = 1;
      this.limit = 8;
      this.getFavoriteList(type, this.page, this.limit);
    },

    turnBanner(link) {
      console.log(link);

      if (link != '') {
        window.location.href = link;
      } else {
        let token = localStorage.getItem('token');

        if (!token) {
          window.open('https://salesiq.zoho.com/signaturesupport.ls?widgetcode=75672d291fd9d5fcab53ffa3194f32598809c21f9b5284cbaf3493087cdd2e0d1a2010ab7b6727677d37b27582c0e9c4', '_blank');
          return;
        }

        this.$router.push('/me/ads/platform');
      }
    },

    getAdsList() {
      let ads_data = {
        page: 1,
        limit: 10000
      };
      ADS_LIST(ads_data).then(res => {
        if (res.code == 200) {
          // console.log(rs.message)
          let adsDataTop = [];
          let identity = localStorage.getItem('identity');

          if (!identity) {
            adsDataTop = res.message.filter(item => item.name == 'guest_h1');
          }

          if (identity == 1) {
            adsDataTop = res.message.filter(item => item.name == 'educator_h1');
          }

          if (identity == 2) {
            adsDataTop = res.message.filter(item => item.name == 'business_h1');
          }

          if (identity == 3) {
            adsDataTop = res.message.filter(item => item.name == 'vendor_h1');
          }

          if (adsDataTop.length > 0) {
            this.adsDataTop = adsDataTop[0].data;
          }
        }
      }).catch(err => {
        if (err.msg) {
          this.$message.error(err.msg);
        }

        if (err.message) {
          this.$message.error(err.message);
        }
      });
    },

    getFavoriteList(type, page, limit) {
      let params = {
        page: page,
        limit: limit
      };

      if (type) {
        params.type = type;
      }

      if (this.favoritedSortValue) {
        params.order_by = this.favoritedSortValue;
      }

      GET_FAVORITE_LIST(params).then(res => {
        console.log(res);

        if (res.code == 200) {
          this.favoriteData = res.message.data; // console.log(res.message.data)

          this.totalNum = res.message.total;
        }
      }).catch(err => {
        console.log(err);

        if (err.msg) {
          this.$message.error(err.msg);
        }

        if (err.message) {
          this.$message.error(err.message);
        }
      });
    },

    pageSizeChange(e) {
      console.log(e);
    },

    pageChange(e) {
      this.page = e;
      this.getFavoriteList(this.type, e, this.limit);
      console.log(e);
    },

    cancelFavorite(type, typeId) {
      let params = {
        token: localStorage.getItem('token'),
        type: type,
        type_id: typeId
      };
      CANCEL_FAVORITE(params).then(res => {
        console.log(res);

        if (res.code == 200) {
          this.getFavoriteList();
        }
      }).catch(err => {
        console.log(err);

        if (err.msg) {
          this.$message.error(err.msg);
        }

        if (err.message) {
          this.$message.error(err.message);
        }
      });
    }

  }
};